<template>
	<div class="poi-picker">
		<div class="poi-picker-search">
			<input type="text" class="form-control poi-picker-search" placeholder="Search" v-model="q" @keyup="searchOnInput">
			<div class="poi-picker-results" v-if="showResults">
				<div class="result" v-for="result in results" :key="result.id" v-html="result.attrs.label" @click="selectLocation(result.attrs.y, result.attrs.x)"></div>
			</div>
		</div>
		<div class="map-container">
			<div ref="map" class="map"></div>
			<div class="layer-switcher btn-group btn-group-sm">
				<button type="button" class="btn" v-bind:class="getButtonClass('topo')" @click.prevent="switchLayer('topo')">Topo</button>
				<button type="button" class="btn" v-bind:class="getButtonClass('ortho')" @click.prevent="switchLayer('ortho')">Ortho</button>
			</div>
			<div class="credits small">&copy;swisstopo</div>
		</div>
		<input id="poi-coordinates" v-model="coordinates" class="form-control" v-bind:name="inputName">
	</div>
</template>

<script>
import axios from 'axios'
import 'ol/ol.css';
import Map from 'ol/Map';
import Projection from 'ol/proj/Projection';
import {Tile as TileLayer, Vector as VectorLayer} from 'ol/layer';
import TileWMS from 'ol/source/TileWMS';
import View from 'ol/View';
import {addCoordinateTransforms, addProjection, transform} from 'ol/proj';
import {Feature} from 'ol/index';
import {Point} from 'ol/geom';
import {Icon, Style} from 'ol/style';
import {Vector as VectorSource} from 'ol/source';

const projection = new Projection({
	code: 'EPSG:21781',
	extent: [485869.5728, 76443.1884, 837076.5648, 299941.7864],
	units: 'm',
});
addProjection(projection);

var poiVectorSource = new VectorSource();

export default {
	props: {},
	data: () => ({
		map: false,
		markerPath: '/img/marker.svg', // default
		coordinates: null,
		active: 'topo',
		q: null,
		showResults: false,
		results: null,
		inputName: null,
		layers: {
			topo: null,
			ortho: null,
			poi: null
		}
	}),
	methods: {
		setFeature(e) {
			this.setPoi(e.coordinate)
		},
		setPoi(coordinates) {
			poiVectorSource.clear()

			var feature = new Feature({
				geometry: new Point(coordinates),
				name: 'POI'
			})
			
			poiVectorSource.addFeature(feature)
			this.coordinates = coordinates
		},
		switchLayer(layer) {
			if (layer == 'ortho') {
				this.layers.topo.setOpacity(0)
				this.layers.ortho.setOpacity(1)
				this.active = 'ortho'
			}
			else {
				this.layers.topo.setOpacity(1)
				this.layers.ortho.setOpacity(0)
				this.active = 'topo'
			}
		},
		getButtonClass(button) {
			if (button == this.active) {
				return 'btn-primary'
			}
			else {
				return 'btn-light'
			}
		},
		searchOnInput() {
			this.results = null
			this.showResults = false
			if (this.q.length > 2) {
				this.searchLocation()
			}
		},
		searchLocation() {
			axios.get('https://api3.geo.admin.ch/rest/services/api/SearchServer?type=locations&origins=gazetteer&searchText='+ this.q).then((result) => {
				this.results = result.data.results
				this.showResults = true
			})
		},
		selectLocation(y, x) {
			this.map.getView().setCenter([y, x])
			this.map.getView().setZoom(7)
			this.showResults = false
		},
		setMarkerPath() {
			var el = document.getElementById("poi-picker-marker-path")
			if (el !== null) {
				this.markerPath = el.innerHTML
			}
		},
		setInputName() {
			var el = document.getElementById("poi-picker-input-name")
			if (el !== null) {
				this.inputName = el.innerHTML
			}
		},
		setInitialPoi() {
			var el = document.getElementById("poi-picker-coordinates")
			if (el !== null) {
				var str = el.innerHTML
				if (str) {
					var coordinates = str.match(/\d+(?:\.\d+)?/g).map(Number)
					this.setPoi(coordinates)
					this.map.getView().setCenter(coordinates)
					this.map.getView().setZoom(7)
				}
			}
		}
	},
	mounted() {	
		this.setMarkerPath()
		this.setInputName()
		
		this.map = new Map({
			target: this.$refs['map'],
			layers: [
				this.layers.ortho = new TileLayer({
					id: "ortho-layer",
					opacity: 0,
					source: new TileWMS({
						url: 'https://wms.geo.admin.ch/',
						params: {
							'LAYERS': 'ch.swisstopo.swissimage',
							'FORMAT': 'image/jpeg',
						},
						serverType: 'mapserver',
					}),
				}),
				this.layers.topo = new TileLayer({
					id: "topo-layer",
					opacity: 1,
					source: new TileWMS({
						url: 'https://wms.geo.admin.ch/',
						params: {
							'LAYERS': 'ch.swisstopo.pixelkarte-farbe',
							'FORMAT': 'image/jpeg',
						},
						serverType: 'mapserver',
					}),
				}),
				this.layers.poi = new VectorLayer({
					id: "poi-layer",
					opacity: 1,
					source: poiVectorSource,
					style: new Style({
						image: new Icon({
							anchor: [20, 58],
							anchorXUnits: 'pixels',
							anchorYUnits: 'pixels',
							src: this.markerPath,
						}),
					}),
				}),
			],
			view: new View({
				projection: projection,
				center: transform([8.4, 46.4], 'EPSG:4326', 'EPSG:21781'),
				zoom: 3,
				minZoom: 3,
				maxZoom: 12
			}),
		});
		
		this.setInitialPoi()
		this.map.on('click', this.setFeature)
	}
}



// We also declare EPSG:21781/EPSG:4326 transform functions. These functions
// are necessary for the ScaleLine control and when calling ol/proj~transform
// for setting the view's initial center (see below).

addCoordinateTransforms(
	'EPSG:4326',
	projection,
	function (coordinate) {
		return [
			WGStoCHy(coordinate[1], coordinate[0]),
			WGStoCHx(coordinate[1], coordinate[0]),
		];
	},
	function (coordinate) {
		return [
			CHtoWGSlng(coordinate[0], coordinate[1]),
			CHtoWGSlat(coordinate[0], coordinate[1]),
		];
	}
);

/*
 * Swiss projection transform functions downloaded from
 * https://www.swisstopo.admin.ch/en/knowledge-facts/surveying-geodesy/reference-systems/map-projections.html
 */

// Convert WGS lat/long (° dec) to CH y
function WGStoCHy(lat, lng) {
  // Converts degrees dec to sex
  lat = DECtoSEX(lat);
  lng = DECtoSEX(lng);

  // Converts degrees to seconds (sex)
  lat = DEGtoSEC(lat);
  lng = DEGtoSEC(lng);

  // Axillary values (% Bern)
  const lat_aux = (lat - 169028.66) / 10000;
  const lng_aux = (lng - 26782.5) / 10000;

  // Process Y
  const y =
    600072.37 +
    211455.93 * lng_aux -
    10938.51 * lng_aux * lat_aux -
    0.36 * lng_aux * Math.pow(lat_aux, 2) -
    44.54 * Math.pow(lng_aux, 3);

  return y;
}

// Convert WGS lat/long (° dec) to CH x
function WGStoCHx(lat, lng) {
  // Converts degrees dec to sex
  lat = DECtoSEX(lat);
  lng = DECtoSEX(lng);

  // Converts degrees to seconds (sex)
  lat = DEGtoSEC(lat);
  lng = DEGtoSEC(lng);

  // Axillary values (% Bern)
  const lat_aux = (lat - 169028.66) / 10000;
  const lng_aux = (lng - 26782.5) / 10000;

  // Process X
  const x =
    200147.07 +
    308807.95 * lat_aux +
    3745.25 * Math.pow(lng_aux, 2) +
    76.63 * Math.pow(lat_aux, 2) -
    194.56 * Math.pow(lng_aux, 2) * lat_aux +
    119.79 * Math.pow(lat_aux, 3);

  return x;
}

// Convert CH y/x to WGS lat
function CHtoWGSlat(y, x) {
  // Converts military to civil and to unit = 1000km
  // Axillary values (% Bern)
  const y_aux = (y - 600000) / 1000000;
  const x_aux = (x - 200000) / 1000000;

  // Process lat
  let lat =
    16.9023892 +
    3.238272 * x_aux -
    0.270978 * Math.pow(y_aux, 2) -
    0.002528 * Math.pow(x_aux, 2) -
    0.0447 * Math.pow(y_aux, 2) * x_aux -
    0.014 * Math.pow(x_aux, 3);

  // Unit 10000" to 1 " and converts seconds to degrees (dec)
  lat = (lat * 100) / 36;

  return lat;
}

// Convert CH y/x to WGS long
function CHtoWGSlng(y, x) {
  // Converts military to civil and to unit = 1000km
  // Axillary values (% Bern)
  const y_aux = (y - 600000) / 1000000;
  const x_aux = (x - 200000) / 1000000;

  // Process long
  let lng =
    2.6779094 +
    4.728982 * y_aux +
    0.791484 * y_aux * x_aux +
    0.1306 * y_aux * Math.pow(x_aux, 2) -
    0.0436 * Math.pow(y_aux, 3);

  // Unit 10000" to 1 " and converts seconds to degrees (dec)
  lng = (lng * 100) / 36;

  return lng;
}

// Convert DEC angle to SEX DMS
function DECtoSEX(angle) {
  // Extract DMS
  const deg = parseInt(angle, 10);
  const min = parseInt((angle - deg) * 60, 10);
  const sec = ((angle - deg) * 60 - min) * 60;

  // Result in degrees sex (dd.mmss)
  return deg + min / 100 + sec / 10000;
}

// Convert Degrees angle to seconds
function DEGtoSEC(angle) {
  // Extract DMS
  const deg = parseInt(angle, 10);
  let min = parseInt((angle - deg) * 100, 10);
  let sec = ((angle - deg) * 100 - min) * 100;

  // Avoid rounding problems with seconds=0
  const parts = String(angle).split('.');
  if (parts.length == 2 && parts[1].length == 2) {
    min = Number(parts[1]);
    sec = 0;
  }

  // Result in degrees sex (dd.mmss)
  return sec + min * 60 + deg * 3600;
}
</script>
